:root {
    --color-1 : #4E4FEB;
    --color-2 : #93BFCF;
    --color-3 : #BDCDD6;
    --color-4 : #EEE9DA;
    --color-gray : #DDDDDD;
    --color-white : #FFFFFF;
    --color-green : #00d853;
    --color-red : #ff2b2b;
}

/* 기본 */
.flx { display: flex; }
.jsc { justify-content: center; }
.aic { align-items: center; }
.mg-t40 { margin-top: 40px; }
/* header */
header {width: 100%; height: 60px; border-bottom: 1px solid #CCC; }
header > .inner { height: 100%; display: flex; justify-content: space-between; align-items: center; }
header > .inner > .logo { font-size: 24px; font-weight: 800; cursor: pointer; }
header > .inner > .header-util { display: flex; }
header > .inner > .header-util > * { font-size: 20px; font-weight: bold; cursor: pointer; }
header > .inner > .header-util > *:not(:last-child) { margin-right: 10px; }
.inner { max-width: 1280px; width: 100%; margin: 0 auto; }

/* content-wrap */
.content-wrap { padding: 15px; }
/* visual */
.visual-wrap { display: flex; width: 100%; height: 400px; background-color: var(--color-1); }
.visual-wrap > .inner { display: flex; height: 100%; align-items: center; }
.visual-wrap > .inner > * { width: 50%; }
.visual-txt { font-size: 32px; font-weight: bold; line-height: 2; color: var(--color-white); }
.visual-txt > b { font-size: 32px; font-weight: bold; color: inherit; }

/* modal */
.modal-wrap { position: fixed; display: flex; width: 100%; height: 100%; padding:0 15px; top:0; left: 0; align-items: center; justify-content: center; background-color: #00000054; }
.modal-wrap.off { display: none; }
.modal-inner { max-width: 1280px; width: 100%; height: 50vh; padding: 0 20px; background-color: #FFF;}
.modal-header { align-items: center; justify-content: flex-end; }
.modal-header > img { position: relative; display: block; width: 42px; right: -10px; cursor: pointer; }
.modal-content { display: flex; width: 100%; height: calc(100% - 40px); flex-direction: column; align-items: center; }

/*text-area*/
.text-area {display: block; width: 100%; min-height: 300px; border:1px solid var(--color-gray);}

/*tag*/
.tag-wrap { display: flex; margin-top: 10px; }
.tag-wrap > li { padding: 5px 10px; background-color: var(--color-gray); font-size: 14px; font-weight: bold; }
.tag-wrap > li:not(:last-child) { margin-right: 10px; }

.view-wrap h2 { font-size: 32px; font-weight: bold; }